/* global document, Office, Word */

Office.onReady((info) => {
  if (info.host === Office.HostType.Word) {
    document.getElementById("ask").onclick = ask;
    document.getElementById("gen").onclick = gen;
  }
});

export async function gen() {
  return Word.run(async (context) => {

    const input = context.document.getSelection().paragraphs.getFirst();
    input.load("text");
    await context.sync();

    var received: string;
    await postData("https://maia.sausheong.com/gen", input.text)
      .then((response) => {
        if (!response.ok) {
          throw new Error(`HTTP error, status = ${response.status}`);
        }
        return response.text();
      })
      .then((text) => {
        received = text;
      });

    const paragraph = context.document.body.insertInlinePictureFromBase64(received, Word.InsertLocation.end);
    await context.sync();
  });
}

export async function ask() {
  return Word.run(async (context) => {

    const input = context.document.getSelection().paragraphs.getFirst();
    input.load("text");
    await context.sync();

    var received: string;
    await postData("https://maia.sausheong.com/ask", input.text)
      .then((response) => {
        if (!response.ok) {
          throw new Error(`HTTP error, status = ${response.status}`);
        }
        return response.text();
      })
      .then((text) => {
        received = text;
      });

    console.log("from server: " + received);

    const paragraph = context.document.body.insertText(received, Word.InsertLocation.end);
    await context.sync();
  });
}

async function postData(url = "", data = "") {
  const response = await fetch(url, {
    method: "POST", 
    body: data, 
  });
  return response;
}

async function getParagraph() {
  await Word.run(async (context) => {
    const paragraph = context.document.getSelection().paragraphs.getFirst();
    paragraph.load("text");

    await context.sync();
    console.log(paragraph.text);
  });
}